<template>
  <v-container fluid>
    <v-flex>
      <v-card>
        <v-card-title class="justify-center">Registro Básico</v-card-title>
        <v-card-text>
          <v-tabs
            fixed-tabs
            background-color="primary"
            dark
            v-model="tab"
          >
            <v-tab key="tab-1">
              1. General
            </v-tab>
            <v-tab key="tab-2">
              2. Codificación
            </v-tab>
            <v-tab key="tab-3">
              3. Declaración
            </v-tab>
          </v-tabs>
          <v-tabs-items v-model="tab">
            <v-tab-item
              key="tab-1"
            >
              <general :email.sync = 'email' :codigo.sync= 'codigo' :dataRegistro.sync = "dataRegistro" />
            </v-tab-item>
            <v-tab-item
              key="tab-2"
            >
              <codificacion :email.sync = 'email' :codigo.sync= 'codigo' />
            </v-tab-item>
            <v-tab-item
              key="tab-3"
            >
              <declaracion :email.sync = 'email' :codigo.sync= 'codigo' :aceptaterminos.sync= 'aceptaterminos' :estadotxt.sync = 'estadotxt' />
            </v-tab-item>
          </v-tabs-items>
        </v-card-text>
      </v-card>
    </v-flex>
  </v-container>
</template>
<script>
import { getAuth } from 'firebase/auth'
import { registroProponentes } from '@/services/api'
export default {
  name: 'formUsuario',
  components: {
    general: () => import('../registro/regGeneral'),
    codificacion: () => import('../registro/regUnspsc'),
    declaracion: () => import('../registro/regDeclaracion')
  },
  data () {
    return {
      email: '',
      dataRegistro: '',
      codigo: '0',
      aceptaterminos: false,
      estadotxt: '',
      e1: -1,
      tab: null
    }
  },
  methods: {
    async getData () {
      try {
        const { data } = await registroProponentes.getDataRegistro({ email: this.email })
        this.dataRegistro = data.data[0]
        if (this.dataRegistro) {
          this.codigo = this.dataRegistro.codigo
          this.aceptaterminos = this.dataRegistro.aceptaterminos
          this.estadotxt = this.dataRegistro.estadotxt
          this.$store.commit('setDataRegistro', this.dataRegistro)
        }
      } catch (error) {
      } finally {
      }
    }
  },
  watch: {
    '$store.state.actuaizaregistro': {
      handler: function (nv) {
        if (nv) {
          this.getData()
          this.$store.commit('updateRegistro', false)
          this.$store.commit('setDataRegistro', this.dataRegistro)
        }
      },
      immediate: true // provides initial (not changed yet) state
    }
  },
  mounted () {
    const auth = getAuth()
    if (auth.currentUser) {
      this.email = auth.currentUser.email
      this.getData()
    }
  }
}
</script>
